.App {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 30px;
    background: linear-gradient(#392d39, #1a1729);
    height: 100vh;
    color: #e27550;
}

@media (max-width: 500px) {
    .logo {
        height: 70%;
        width: 70%;
    }
    .App {
        font-size: 20px;
    }
}

